

















import Vue from "vue";

export default Vue.extend({
  name: "ProjectDetailsOverlay",
  props: {
    visible: {
      type: Boolean,
      required: true
    },
    color: String,
    title: String,
    htmlContent: String,
  },
  watch: {
    visible(newValue) {
      if (newValue) {
        document.body.classList.add('no-scroll');
        console.log("run");
      } else {
        document.body.classList.remove('no-scroll');
        console.log("run nos");
      }
    }
  },
  methods: {
    getImage: function(url: string) {
      console.log("fetching image " + url);
    }
  }
});
